import { PlatformLocation } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { ModalController, NavParams } from '@ionic/angular';
import { Model } from 'src/app/api/models/model';
import { RequestService } from 'src/app/api/request.service';
import { DialogService } from 'src/app/api/util/dialog.service';
import { ModalFormInterface } from 'src/app/interfaces/modal-form-interface';
import { ModalForm } from 'src/app/models/modal-form';

@Component({
  selector: 'app-category-form',
  templateUrl: './category-form.page.html',
  styleUrls: ['./category-form.page.scss'],
})
export class CategoryFormPage extends ModalForm implements OnInit , ModalFormInterface  {

  public modelCategory : Model;
  public category : any;
  public id : any;

  constructor(
    public request : RequestService,
    public viewCtrl: ModalController,
    navParams: NavParams,
    public dialogService : DialogService,
    location: PlatformLocation
  ) { 

    super(location,viewCtrl)

    this.modelCategory = new Model('Category',request);
    this.id = navParams.get('id') ;

  }


  initObject(){
    
    console.log('initObject');

    this.category = { 
      name : '',
    };  

    if(this.id){
      this.modelCategory.api_find(this.id).subscribe(data => {
        console.log('load category', data)
        if(data['status'] == 'success'){
          this.category = data['Category'];
        }
      })
    }


  }

  submit(){
    if(this.category.id){
      this.modelCategory.api_update(this.category).subscribe(data => {
        if(data['status'] == 'success'){
          this.dialogService.showToast( data['msjSuccess'] ?? 'Registrado' );
          this.dismiss(data['Category'])
        }
      });

    }else{
      this.modelCategory.api_create(this.category).subscribe(data => {
        if(data['status'] == 'success'){
          this.dialogService.showToast( data['msjSuccess'] ?? 'Registrado' );
          this.dismiss(data['Category'])
        }
    })
    }
  }
  

  dismiss(category) {
    this.viewCtrl.dismiss(
      {
        category : category,
      }
    );
  }

  close(){
    this.viewCtrl.dismiss();
  }

}
