import { Component, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { Model } from 'src/app/api/models/model';
import { RequestService } from 'src/app/api/request.service';
import { DialogService } from 'src/app/api/util/dialog.service';
import { CategoryFormPage } from '../category-form/category-form.page';
//import { CategoryFormPage } from '../category-form/category-form.page';

@Component({
  selector: 'app-list',
  templateUrl: './list.page.html',
  styleUrls: ['./list.page.scss'],
})
export class ListPage implements OnInit {

  public categoryService : Model;


  constructor(
    public request : RequestService,
    public modalController: ModalController,
    public dialog : DialogService
  ) {
    this.categoryService = new Model('Category',request);
  }
  
  ngOnInit(): void {
    
    this.categoryService.api_all();
  }


  async openModal() {
    const modal = await this.modalController.create({
      component: CategoryFormPage ,
    });

    modal.onDidDismiss().then(data=>{
      const category = data.data['category'];
      this.categoryService.listAddLast(category);
    })

    return await modal.present();
   }

   async showEdit(category){
    const modal = await this.modalController.create({
      component: CategoryFormPage ,
      componentProps: { id: category.id }
    });

    modal.onDidDismiss().then(data=>{
      console.log(data);
      this.categoryService.listUpdate(category.id,category);

    })

    return await modal.present();
   }

   confirmDelete(category){
    this.dialog.presentAlertConfirm('Confirmar','Estas seguro de eliminar este Categor√≠a',e => {
      this.delete(category)
    });
   }

   delete(category){
     this.categoryService.api_delete(category.id).subscribe(data => {
       console.log(data);
       if(data['status'] == 'success'){
         this.dialog.showToast('Categor√≠a Eliminado',null,'success');
         this.categoryService.listDelete(category.id);
       }
     });
   }



}
